import Vue from 'vue'
import App from './App.vue'

//  Element_ui
import ElementUI from 'element-ui'
//  Element-ui 
import 'element-ui/lib/theme-chalk/index.css'
//  Element-ui
Vue.use(ElementUI)

// 
import './assets/index.css'

// 
import VueRouter from 'vue-router'
Vue.use(VueRouter)

// 
import discovery from './views/01.discovery.vue'
import playlists from './views/02.playlists.vue'
import songs from './views/03.songs.vue'
import mvs from './views/04.mvs.vue'
import result from './views/05.result.vue'
import playlist from './views/06.playlist.vue'
import mv from './views/07.mv.vue'

const router = new VueRouter({
    routes: [{
            path: '/',
            redirect: '/discovery'
        },
        {
            // 
            path: '/discovery',
            component: discovery
        },
        {
            // 
            path: '/playlists',
            component: playlists
        },
        {
            // 
            path: '/playlist',
            component: playlist
        },
        {
            // 
            path: '/songs',
            component: songs
        },
        {
            // 
            path: '/mvs',
            component: mvs
        },
        // mv
        {
            path: '/mv',
            component: mv
        },
        // 
        {
            path: '/result',
            component: result
        }
    ]
})

Vue.config.productionTip = false

new Vue({
    render: h => h(App),
    // Vue
    router
}).$mount('#app')